import Vue from 'vue'
import {queryUserById} from '@/static/js/API.js'
const User = require('@/static/js/Utils.js').User
var _this;
export default {
	data() {
		return {
		};
	},
	onShow:function() {   
		uni.getSystemInfo({
			success: function(e) {   						



				








						






						  




		   }
		})
	},
	onLaunch() {
		_this = this;
		console.log('App onshow')
		 
		  uni.getStorage({
			key:"user",
			success:(res)=>{
			   console.log(res) 
			   if(res.data != null){  //每次启动去数据库中同步一次用户信息,包括课表信息，周次信息，学期信息
					let user = res.data
					let url = `${queryUserById}?id=${user.id}`
					uni.request({
						url:url,
						method:'POST',
						dataType:'json'  
					})
					.then(res2=>{
						console.log("同步到的用户信息",res2)
						if(res2[1].statusCode == 200 && res2[1].data.data.user != null){
							uni.setStorage({
								key:'user',
								data:res2[1].data.data.user,
								success() {
									uni.setStorage({
										key:'course',
										data:res2[1].data.data.courses,
										success() {
											uni.setStorage({
												key : 'nowTerm',
												data:res2[1].data.data.nowTerm,
												success() {
													uni.setStorage({
														key:'nowWeek',
														data:res2[1].data.data.nowWeek
													})
												}
											})
										},
										fail() {   //设置course失败则为老师
											uni.redirectTo({
												url: '/pages/service/service'
											});
										}
									})
								},
							})
						}
					})
					.catch(Error=>{ 
						console.log("启动同步用户信息错误",Error)
						
					})     
				   }else{
					   uni.redirectTo({
						url: '/pages/login/login'
					   });
				   }
				},
				fail:() => {
					uni.redirectTo({
						url: '/pages/login/login'
					});
				}
			  })		
	}, 
	onHide: function() {
		console.log('App 关闭')
	}
}
